import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import images from './helpers.js';
import logo from './images/relativity-formulae.svg';
import '../node_modules/react-image-gallery/styles/css/image-gallery.css';
import './App.css';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Loader from 'react-loader-spinner'
import Modal from 'react-bootstrap/Modal';
const robodog = require('@uppy/robodog');

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      font: 'Ubuntu',
      fontSize: 12,
      message: 'Default statement',
      imageIndex: 0,
      show: false,
      returnedImage: '',
      loading: false
    };
    this.generateImage = this.generateImage.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleImage = this.handleImage.bind(this);
    this.modalClose = this.modalClose.bind(this);
  }

  
  async generateImage(e){
    return fetch(images[this.state.imageIndex].original)
    .then((response)=> response.blob())
    .then((blob) => {
      const resultPromise = robodog.upload([blob],{
        params: {
          auth: { key: 'ee723b70d56f11e8b7809f4882ea3f24' },
        "steps": {
          ":original": {
            "robot": "/upload/handle"
          },
          "resized": {
            "use": [
              ":original"
            ],
            "robot": "/image/resize",
            "result": false,
            "height": 500,
            "imagemagick_stack": "v1.0.0",
            "resize_strategy": "fit",
            "width": 500,
            "zoom": false
          },
          "watermarked": {
            "use": [
              "resized"
            ],
            "robot": "/image/resize",
            "result": true,
            "imagemagick_stack": "v1.0.0",
            "text": [
              {
                "text": this.state.message,
                "size": parseInt(this.state.fontSize),
                "font": this.state.font,
                "color": "#eeeeee",
                "valign": "bottom",
                "align": "right",
                "x_offset": 16,
                "y_offset": -10
              }
            ]
          }
        }
      },
      waitForEncoding: true
      })
      resultPromise.then((bundle) =>{
        const safeurl = bundle.successful[0].meta.assembly_url.replace(/^http:\/\//i, 'https://');
        fetch(safeurl)
        .then((results)=>results.json())
        .then(data =>{
          this.setState({
            show:true,
            loading: false,
            returnedImage: data.results['watermarked'][0].ssl_url
          })
        });
      })    
      }
    )
  }
  
  handleSelect(e){
    e.preventDefault();
    let key = e.target.name;
    let value = e.target.value;
    this.setState({[key]: value});
  }

  handleImage(e){
    this.setState({imageIndex: e});
  }

  modalClose(){
    this.setState({show:false});
  }

  render() {
    let loading = this.state.loading;
    return loading ?
    <Loader
    type="CradleLoader"
    color="#000000"
    height="100"
    width="100"
    />
    :
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <h1>Select An Einstein</h1>
        <Modal show={this.state.show} onHide={this.modalClose} size="lg">
            <Modal.Header closeButton>
            <Modal.Body>
              <img 
                src={this.state.returnedImage}
                alt="new"
                />
            </Modal.Body>
            </Modal.Header>
          </Modal>
        <ImageGallery onSlide={(e)=>this.handleImage(e)} items={images} showThumbnails={false} showFullscreenButton={false} showPlayButton={false}/>
        <Form>
          <Form.Label>Font</Form.Label>
          <Form.Control name="font" as="select" onChange={(e)=> this.handleSelect(e)}>
            <option>Ubuntu</option>
            <option>Arial</option>
            <option>Comic Sans MS</option>
          </Form.Control>
          <Form.Label>Font Size</Form.Label>
          <Form.Control name="fontSize" as="select" onChange={(e)=> this.handleSelect(e)}>
            <option>12</option>
            <option>18</option>
            <option>24</option>
          </Form.Control>
          <Form.Label>Personal Message</Form.Label>
          <Form.Control type="input" placeholder="Your message" />
          <br/>
        </Form>
        <Button onClick={(e)=> this.generateImage(e)} variant="outline-primary" >Create My Einstein</Button>
        <br/>
        <div>Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0">CC 3.0 BY</a></div>
      </div>
  }
}

export default App;